.chatPage{
    background-color: rgb(31,29,29);
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.chatContainer{
    background-color: white;
    height: 60%;
    width: 50%;
}

.header {
    background-color: crimson;
    height: 15%;
    display: flex;
    justify-content: space-between;
  }
  
  .header > h2 {
    color: white;
    margin: 1.5vmax;
  }
  
  .header > a {
    margin: 1.5vmax;
    transition: all 0.3s;
    cursor: pointer;
  }
  
  .header > a:hover {
    transform: scale(1.2);
  }
  
  .header > a > img {
    height: 100%;
  }
  
  .header > a > img:hover {
    filter: brightness(80%);
  }
  
.chatBox{
    height: 70%;
    box-sizing: border-box;
}

.inputBox{
    
    /* border: 0.2vmax solid black; */
    height: 15%;
    display: flex;
}

#chatInput{
    width: 80%;
    border: none;
    padding: 2vmax;
    box-sizing: border-box;
    background-color: white;
    outline: none;
    font-size: 1.2vmax;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border-top: 0.2vmax solid black;
}

.sendBtn{
    background-color: crimson;
    color: white;
    border: none;
    width: 21%;
    transition: all 0.3s;
    cursor: pointer;
}
.sendBtn>img{
    width: 5vmax;
    color: white;
    filter: brightness(100);
    transition: all 0.3s;
}

.sendBtn:hover{
    background-color: rgb(182, 14, 48);
}

.sendBtn:hover img{
    transform: translateX(10px);
}

